body {
  background-color: darkolivegreen;
}
#number-wrapper {
  display: grid;
  grid-template-columns: 20vh 20vh 20vh;
  grid-template-rows: 20vh 20vh 20vh 20vh;
  position: absolute;
  top: 10vh;
  left: calc(50vw - 60vh);
  border-radius: 5px;
  background-color: gray;
}

#operator-wrapper {
  background-color: gray;
  position: absolute;
  top: 10vh;
  left: calc(50vw - 40vh);
  display: grid;
  grid-template-columns: 10vh 10vh 10vh 10vh;
  grid-template-rows: 18vh;
  height: 18vh;
  padding-top: 2vh;
  border-radius: 5px;
}

button {
  border-radius: 5px;
  font-size: 10vh;
  background-color:silver;
}

#add {
  background-color: #cc0000;
}

#subtract {
  background-color: #3d85c6;
}

#multiply {
  background-color: #6aa84f;
}

#divide {
  background-color: #f1c232;
}

#clear-wrapper {
  font-size: 10vh;
  position: absolute;
  background-color: gray;
  width: 60vh;
  height: 80vh;
  top: 10vh;
  z-index: -10;
  left: 50vw;
  border-radius: 5px;
}

#clear {
  font-size: 10vh;
  position: absolute;
  top: 60vh;
  background-color: white;
  color: black;
  width: 60vh;
  height: 18vh;
}

#equals {
  font-size: 10vh;
  position: absolute;
  width: 40vh;
  height: 20vh;
  border-radius: 5px;
  top: 50vh;
  left: calc(50vw + 20vh);
}

#decimal {
  font-size: 10vh;
  position: absolute;
  border-radius: 5px;
  width: 20vh;
  height: 20vh;
  top: 50vh;
  left: 50vw;
}

#display {
  position: absolute;
  word-wrap: break-word;
  width: 60vh;
  height: 40vh;
  text-align: center;
  top: 10vh;
  left: 50vw;
  border-radius: 5px;
  font-size: 60px;
  background-color: black;
  color: white;
}